import React, { useEffect, useState } from 'react'
import IconLabelButton from './IconLabelButton'

function SaveButton() {

  const [open, setOpen] = useState(false)

  useEffect(() => {
    const timer = setTimeout(() => {
      setOpen(false)
    }, 10000);
    return () => clearTimeout(timer);
  }, []);

  return (
    <div>
      {open && <div class="notification is-primary" onClick={() => setOpen(false)} style={{ position: 'absolute', zIndex: 2, top: -40, right: 0 }}>
        <button class="delete"></button>
          This job posting has been saved to your shortlist, playa!
      </div>}

      <IconLabelButton
        label='Save'
        icon='fas fa-save'
        style={{ fontSize: 16, color: '#D1D1D6' }}
        labelStyle={{ color: '#D1D1D6', fontWeight: 600, fontSize: 12 }}
        onClick={() => setOpen(true)}
      />
    </div>
  );
}

export default SaveButton