import React from 'react'
import { Purple } from './colors'

function Tabs(params) {
  return (
    <div class="columns is-mobile">
      {params.tabs && params.tabs.map((name, index) => (
        <div
          class="column"
          key={index}
          style={{
            marginRight: (index === params.tabs.length - 1 ? 0 : 10),
            paddingRight: 20,
            color: (params.position === index ? 'black' : Purple),
            fontWeight: 'bold',
            borderBottom: 'solid 2px ' + (params.position === index ? 'black' : Purple),
            cursor: 'pointer',
            position: 'relative'
          }}
          onClick={() => params.setPosition(index)}
        >
          <h5>{name}</h5>
        </div>
      ))}
    </div>
  )
}

export default Tabs
