import React from 'react'
import { LightGrey } from '../global/colors'
import TechTag from '../global/tags/TechTag'
import ExternalLink from '../global/ExternalLink'

function JobViewExtraInfoSubSection(params) {
    return (
        <div>
            <h6 color={LightGrey}>WEBSITE</h6>
            <p style={{ marginBottom: 12 }}><ExternalLink href={params.website} /></p>
            <h6 color={LightGrey}>TECH STACK</h6>
            <p style={{ marginBottom: 12 }}>
                {params.techStack && params.techStack.map(text => (
                    <TechTag key={text} label={text} />
                ))}
            </p>
            <h6 size='18px' margin='none' color={LightGrey}>LOCATION</h6>
            <p style={{ marginBottom: 12 }}>{params.location}</p>
        </div>
    );
}

export default JobViewExtraInfoSubSection;
