import React from 'react'

function Stepper(params) {
    return (
        <div className="steps">
            {params.steps && params.steps.map((step, index) => (
                <div
                    onClick={() => params.setIndex(index)}
                    style={{ cursor: 'pointer' }}
                    key={index}
                    className={
                        `step-item  ${params.index >= index ? "is-active" : ""} ${params.isCompleted ? "is-completed is-success" : ""}`
                    }
                >
                    <div className="step-marker">
                        {step.isCompleted && (index !== params.steps.length - 1) && <i className='fas fa-check' />}
                        {params.finalIcon && (index === params.steps.length - 1) && <i className={params.finalIcon} style={{ fontSize: 14, marginRight: 2 }} />}
                    </div>
                    <div className="step-details">
                        <p className="step-title">{step.title}</p>
                    </div>
                </div>
            ))}
        </div>
    )
}

export default Stepper