import React, { useState } from 'react'
import DeletableTechTag from './global/tags/DeletableTechTag'
import ListInput from './ListInput'
import RadioInput from './RadioInput'

function PostBasicInfo(params) {
  const [techStackInput, setTechStackInput] = useState('');
  const onChangeTechStackInput = function (event) {
    if (event.key === 'Enter') {
      let techStack = [...params.techStack]
      techStack.push(techStackInput)
      params.setTechStack(techStack)
      setTechStackInput('')
    }
  }

  const onClickTechStackInput = () => {
    let techStack = [...params.techStack]
    techStack.push(techStackInput)
    params.setTechStack(techStack)
    setTechStackInput('')
  }

  const onDeleteTechStack = function (index) {
    return function (event) {
      let techStack = [...params.techStack]
      techStack.splice(index, 1);
      params.setTechStack(techStack)
    }
  }

  return (
    <div>
      <div className="columns">
        <div className="column is-6">
          <h3>Job Title</h3>
          <input
            className="input"
            type="text"
            placeholder="Java developer"
            onChange={event => params.setPosition(event.target.value)}
            value={params.position}
          />
        </div>
      </div>
      <div className="columns">
        <div className="column is-6">
          <h3>Tech Stack</h3>
          <ListInput
            value={techStackInput}
            onChange={event => setTechStackInput(event.target.value)}
            onKeyPress={onChangeTechStackInput}
            placeholder='Type a technology'
            iconOnClick={onClickTechStackInput}
          />
          {params.techStack && params.techStack.map((text, i) => (
            <DeletableTechTag
              key={text}
              label={text}
              onClick={onDeleteTechStack(i)}
            />
          ))}
        </div>
      </div>
      <div className="columns">
        <div className="column is-6">
          <h3>Location</h3>
          <input
            className="input"
            type="text"
            placeholder="London, UK"
            onChange={event => params.setLocation(event.target.value)}
            value={params.location}
          />
        </div>
      </div>
      <h3>Employment Type</h3>
      <RadioInput
        name='employmentType'
        value={params.employmentType}
        onChange={params.setEmploymentType}
        options={[
          {
            label: 'Permanent',
            value: 'permanent',
            checked: () => params.employmentType === 'permanent',
            onChange: event => params.setEmploymentType(event.target.value)
          },
          {
            label: 'Freelance',
            value: 'freelance',
            checked: () => params.employmentType === 'freelance',
            onChange: event => params.setEmploymentType(event.target.value)
          },
          {
            label: 'Part-time',
            value: 'parttime',
            checked: () => params.employmentType === 'parttime',
            onChange: event => params.setEmploymentType(event.target.value)
          },
          {
            label: 'Internship',
            value: 'internship',
            checked: () => params.employmentType === 'internship',
            onChange: event => params.setEmploymentType(event.target.value)
          }]
        }
      />
      <h3>Remote Working</h3>
      <RadioInput
        name='remote'
        value={params.remoteWorking}
        onChange={params.setRemoteWorking}
        options={[
          {
            label: 'Fully',
            value: 'fully'
          },
          {
            label: 'Mostly',
            value: 'mostly'
          },
          {
            label: 'Once a week',
            value: 'once'
          },
          {
            label: 'When needed',
            value: 'when'
          },
          {
            label: 'Very rarely',
            value: 'rarely'
          },
          {
            label: 'None',
            value: 'none'
          }]
        }
      />

      <h3>Business Travel</h3>
      <RadioInput
        name='businessTravel'
        value={params.businessTravel}
        onChange={params.setBusinessTravel}
        options={[
          {
            label: '100%',
            value: '100'
          },
          {
            label: '75%',
            value: '75'
          },
          {
            label: '50%',
            value: '50'
          },
          {
            label: '25%',
            value: '25'
          },
          {
            label: 'None',
            value: '0'
          }]
        }
      />

    </div >
  );
}

export default PostBasicInfo;